<template>
  <div class="page">
    <div class="qieScene">
      <ul>
        <li @click="sceneIndex=0" :class="{cur:sceneIndex===0}">
          <div class="img">
            <img src="thumb-1.jpg">
          </div>
        </li>
        <li @click="sceneIndex=1" :class="{cur:sceneIndex===1}">
          <div class="img">
            <img src="thumb-2.jpg">
          </div>
        </li>
        <li @click="sceneIndex=2" :class="{cur:sceneIndex===2}">
          <div class="img">
            <img src="thumb-3.jpg">
          </div>
        </li>
        <li @click="sceneIndex=3" :class="{cur:sceneIndex===3}">
          <div class="img">
            <img src="thumb-4.jpg">
          </div>
        </li>
      </ul>
    </div>
    <div class="bg">
      <canvas id="myCanvas"></canvas>
      <!--      <img :src="bginfo.pic" v-if="bginfo.pic">-->
      <!--      <video :src="bginfo.video" v-else preload="auto" autoplay loop muted @click.stop="openvo"></video>-->
      <!--      <div class="text">-->
      <!--        <span>{{bginfo.picname}}</span>-->
      <!--        <span>{{bginfo.picadd}}</span>-->
      <!--      </div>-->
    </div>
    <div class="content" style="flex-direction: column">
      <div class="top">
        <router-link tag="a" :to="'/articles/'+articles.id">
          <div class="time">{{articles.time}}</div>
          <div class="title">{{articles.title}}</div>
          <div class="img"><img :src="articles.tpic"></div>
        </router-link>
      </div>
      <div class="top">
        <router-link tag="a" :to="'/lifes/'+lifes.id">
          <div class="time">{{lifes.time}}</div>
          <div class="title">{{lifes.title}}</div>
          <div class="img"><img :src="lifes.tpic"></div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import {list} from '@/api/api'
import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';


export default {
    name: 'index',
    data() {
        return {
            sw: window.innerWidth,
            sh: window.innerHeight,
            bginfo: {},
            articles: {},
            lifes: {},
            sceneIndex: 0,
            scene: null,
            camera: null,
            controls: null,
            renderer: null,
            mixer: [],
            clock: []

        }
    },
    mounted() {
        //场景，摄影机
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x46bd87);
        this.camera = new THREE.PerspectiveCamera(50, window.innerWidth / window.innerHeight, 0.01, 1000);
        //渲染
        this.renderer = new THREE.WebGLRenderer({antialias: true, canvas: document.querySelector('#myCanvas')});
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        //控制器
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);

        window.addEventListener('resize', () => {
            this.camera.aspect = window.innerWidth / window.innerHeight;
            this.camera.updateProjectionMatrix();
            this.renderer.setSize(window.innerWidth, window.innerHeight);

        });
    },
    created() {
        let data1 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'bgimg',
            perpage: 1000
        }
        let data2 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'article',
            perpage: 1000,
            order: ["mtime DESC", "id DESC"]
        }
        let data3 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'life',
            perpage: 1000,
            order: ["mtime DESC", "id DESC"],
            where: ["tj = '1'"],
        }


        list(data1).then((data) => {
            data = data.data.list
            let xh = Math.floor(Math.random() * data.length)
            this.bginfo = data[xh]
        })
        list(data2).then((data) => {
            data = data.data.list
            this.articles = data[0]
        })
        list(data3).then((data) => {
            data = data.data.list
            this.lifes = data[1]
        })
    },
    methods: {
        openvo() {
            let target = event.currentTarget
            target.muted = false
        },
        animate() {
            this.mixer.forEach((el, xh) => {
                el.update(this.clock[xh].getDelta() * 1.5);
            })
            this.controls.update();
            this.renderer.render(this.scene, this.camera);
            requestAnimationFrame(this.animate.bind(this));
        },
        resetScene() {
            this.scene.clear()
        },
        addLight() {
            const ambientLight = new THREE.AmbientLight(0xffffff, 1);
            this.scene.add(ambientLight);
            const dirLight = new THREE.DirectionalLight(0xffffff, 4);
            dirLight.position.set(0, 10, 0);
            this.scene.add(dirLight);
        },
        autoRotation() {
            //this.controls.autoRotate = true

        }
    },
    computed: {},
    watch: {
        sceneIndex: {
            immediate: true,
            handler(value) {
                this.$nextTick(() => {
                    this.resetScene()
                    this.addLight()
                    this.autoRotation()
                    if (value === 0) {
                        this.scene.background=new THREE.Color(0x5997D2)
                        this.camera.position.set(0, 0, 2);
                        this.scene.rotation.set(0,0,0)
                        // //载入GITF
                        let mixerP1 = null, mixerP2 = null
                        let loader = new GLTFLoader();
                        loader.load([value + '/sc.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                        });
                        let loaderP1 = new GLTFLoader();
                        loaderP1.load([value + '/plane1.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                            gltf.scene.scale.set(.02, .02, .02)
                            gltf.scene.position.set(-.4, .4, -.4)
                            mixerP1 = new THREE.AnimationMixer(gltf.scene);
                            this.mixer.push(mixerP1)
                            this.clock.push(new THREE.Clock())
                            mixerP1.clipAction(gltf.animations[0]).play();

                        });
                        let loaderP2 = new GLTFLoader();
                        loaderP2.load([value + '/plane2.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                            gltf.scene.scale.set(.01, .01, .01)
                            gltf.scene.position.set(.4, .3, .4)
                            mixerP2 = new THREE.AnimationMixer(gltf.scene);
                            this.mixer.push(mixerP2)
                            this.clock.push(new THREE.Clock())
                            mixerP2.clipAction(gltf.animations[0]).play();
                        });
                    }
                    if (value === 1) {
                        let texture = new THREE.TextureLoader().load(value + '/bg.jpg');
                        texture.colorSpace = THREE.SRGBColorSpace
                        this.scene.background=texture
                        this.camera.position.set(0, 0, -10);
                        this.scene.rotation.set(0,0,0)
                        //载入GITF
                        let mixer = null
                        let loader = new GLTFLoader();
                        loader.load([value + '/sc.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                            gltf.scene.position.set(0, -2, 0)
                            mixer = new THREE.AnimationMixer(gltf.scene);
                            this.mixer.push(mixer)
                            this.clock.push(new THREE.Clock())
                            mixer.clipAction(gltf.animations[0]).play();
                        });
                    }
                    if (value === 2) {
                        let texture = new THREE.TextureLoader().load(value + '/bg.jpg');
                        texture.colorSpace = THREE.SRGBColorSpace
                        this.scene.background=texture
                        this.camera.position.set(0, 21, 120);
                        this.scene.rotation.set(0,0,0)
                        //载入GITF
                        let mixer = null
                        let loader = new GLTFLoader();
                        loader.load([value + '/sc.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                            gltf.scene.position.set(0, -43, 0)
                            mixer = new THREE.AnimationMixer(gltf.scene);
                            this.mixer.push(mixer)
                            this.clock.push(new THREE.Clock())
                            mixer.clipAction(gltf.animations[0]).play();
                        });
                    }
                    if (value === 3) {
                        let texture = new THREE.TextureLoader().load(value + '/bg.jpg');
                        texture.colorSpace = THREE.SRGBColorSpace
                        this.scene.background=texture
                        this.camera.position.set(0, 0, -1);
                        this.scene.rotation.set(0,Math.PI,0)
                        //载入GITF
                        let mixer = null
                        let loader = new GLTFLoader();
                        loader.load([value + '/sc.glb'], (gltf) => {
                            this.scene.add(gltf.scene);
                            gltf.scene.scale.set(3, 3, 3)
                            gltf.scene.position.set(0, -.1, 0)
                            mixer = new THREE.AnimationMixer(gltf.scene);
                            this.mixer.push(mixer)
                            this.clock.push(new THREE.Clock())
                            mixer.clipAction(gltf.animations[0]).play();
                        });
                    }

                    this.animate()
                })
            }
        }
    }
}
</script>

